import React from "react";
import { Routes, Route } from "react-router";
import Knowledge from "components/pages/knowledgePage/Knowledge";
import Experience from "components/pages/experiencePage/Experience";
import Homepage from "components/pages/homePage/Homepage";

const pages = require('../data/pages');

const pageComponents = {
    'Homepage': Homepage,
    'Knowledge': Knowledge,
    'Experience': Experience
}

//pass down the navigator ref to all specific pages
function PageChooser(props) {
    const SpecificPage = pageComponents[props.page];
    return <SpecificPage navigator={props.navigator}/>
}

export default function Router({ navigator }) {
    return (
        <Routes>
            <Route key="home" path="/" element={<Homepage navigator={navigator} />} />
            {pages.map((page) =>
                <Route key={page.name} path={page.path} element={<PageChooser navigator={navigator} page={page.name} />}>
                    <Route path=":subPage"></Route>
                </Route>
            )}
        </Routes>
    );
}