import './scss/App.scss';
import React, { useEffect, useRef } from 'react';
import { HashRouter } from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import Navigator from './components/Navigator';
import Routes from './components/Routes';
//all the images we want to preload 
import knowledgeHeaderBackgroundImage from 'assets/images/knowledge_background-min.jpg';
import experienceHeaderBackgroundImage from 'assets/images/experience_background-min.jpg'

/**
 * This is the main react component. Take note that we need to use HashRouter here instead of BrowserRouter as 
 * this website was made for github pages, and routing in github pages required the hashrouter to ensure it worked properly.
 */

const imagesToPreload = [
  knowledgeHeaderBackgroundImage,
  experienceHeaderBackgroundImage
];
function App() {
  useEffect(() =>
    imagesToPreload.forEach(image => new Image().src = image)
  );
  const navigatorRef = useRef();
  return (
    <HashRouter className="fill" basename={process.env.PUBLIC_URL}>
      <Container fluid className="App p-0 fill h-100">
        <Navigator ref={navigatorRef}></Navigator>
        <Routes navigator={navigatorRef} />
      </Container>
    </HashRouter>
  );
}

export default App;
